
































import { Vue, Component } from 'vue-property-decorator';

import { EFormType, EFormMode, EPageAction } from '@/../enums';
import { ServiceDialog, ServiceResponseFilter, ServiceServerConfig, ServiceFormConfig, StorageUser } from '@/../helpers';
import { IComponent, IStorage } from '@/../models';

import { ServerApiPath, WebPagePath } from '@/config';

// Component
import Card from '@/../components/card/card.vue';
import Loading from '@/../components/loading/loading.vue';
import Forms from '@/../components/form/forms.vue';
import FormPassword from '@/../components/form/form_password.vue';

// form
interface IFormItem {
    isClearState: boolean;
    action: EPageAction;
}

interface IFormData {
    oldPassword: string;
    password: string;
    confirmPassword: string;
}

interface IFormConfigs {
    oldPassword: IComponent.IFormConfig;
    password: IComponent.IFormConfig;
}

interface IFormButtonDisabled {
    oldPassword: boolean;
    password: boolean;
    confirmPassword: boolean;
}

@Component({
    components: { Card, Forms, FormPassword },
})
export default class VuePageClass extends Vue {
    private formItem: IFormItem = { isClearState: true, action: EPageAction.edit };

    private formDataOriginal: IFormData = {
        oldPassword: undefined,
        password: undefined,
        confirmPassword: undefined,
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formConfigOriginal: IFormConfigs = {
        oldPassword: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        password: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfig: IFormConfigs = { ...this.formConfigOriginal };
    private formConfigs: IComponent.IFormConfig[] = [];

    private formButtonDisabled: IFormButtonDisabled = { oldPassword: true, password: true, confirmPassword: true };

    private eFormMode = EFormMode;
    private loading = new Loading();

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        this.initFormData();
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////
    private initFormData(): void {
        this.initFormConfig();
    }

    private initFormConfig(): void {
        // oldPassword
        this.formConfig.oldPassword.id = 'oldPassword';
        this.formConfig.oldPassword.label = this.$i18n.Passwoed_CurrentPassword;
        this.formConfig.oldPassword.placeholder = this.$i18n.Passwoed_CurrentPassword;
        this.formConfig.oldPassword.formType = EFormType.password;
        this.formConfig.oldPassword.required = true;

        // password
        this.formConfig.password.id = 'password';
        this.formConfig.password.formType = EFormType.slot;

        this.formConfigs = [this.formConfig.oldPassword, this.formConfig.password];
    }

    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {}

    private async initSelectOptionXXX(): Promise<void> {}

    //////////////////// Form Show ////////////////////

    private formSaveDisable(): boolean {
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string): void {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////

    private async dataSave(): Promise<void> {
        let data: IFormData = this.formData;
        let param: {
            previous: string;
            current: string;
        } = {
            previous: data.oldPassword,
            current: data.password,
        };

        this.loading.show();

        let content: string = this.$i18n.Passwoed_ChangePasswordSuccess;
        let title: string = this.$i18n.Status_Success;
        ServiceDialog.SuccessConfirm(this, content, title)
            .then((result) => {
                if (result.value) this.logout();
            })
            .catch((e: any) => console.error(e));

        this.loading.hide();
    }

    private async logout(): Promise<void> {
        // Clear Session
        StorageUser.clearStorage();

        this.$router.push(WebPagePath.Login);
    }
}
